.checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgray;
    padding: 15px 0;
    font-size: 20px;
    align-items: center;

    .image-container {
        width: 30%;
        padding-right: 15px;

        img {
            height: 100%;
            width: 100px;
        }
    }

    .name,
    .quantity,
    .price {
        width: 30%;
    }

    .price {
        padding-left: 20px;
        display: flex;
        flex-direction: row;

        .arrow {
            cursor: pointer;
        }
        
        .value {
            margin: 0 10px
        }
    }

    .remove-button {
        padding-left: 12px;
        cursor: pointer;
    }
}